* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
@font-face {
  font-family: 'Circe Regular';
  src: url('../fonts/Circe-Regular.eot');
  src: local('Circe'), local('Circe-Regular'), url('../fonts/Circe-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/Circe-Regular.woff') format('woff'), url('../fonts/Circe-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Circe ExtraLight';
  src: url('../fonts/Circe-ExtraLight.eot');
  src: local('Circe ExtraLight'), local('Circe-ExtraLight'), url('../fonts/Circe-ExtraLight.eot?#iefix') format('embedded-opentype'), url('../fonts/Circe-ExtraLight.woff') format('woff'), url('../fonts/Circe-ExtraLight.ttf') format('truetype');
  font-weight: 200;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Circe Thin';
  src: url('../fonts/Circe-Thin.eot');
  src: local('Circe Thin'), local('Circe-Thin'), url('../fonts/Circe-Thin.eot?#iefix') format('embedded-opentype'), url('../fonts/Circe-Thin.woff') format('woff'), url('../fonts/Circe-Thin.ttf') format('truetype');
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Circe Light';
  src: url('../fonts/Circe-Light.eot');
  src: local('Circe Light'), local('Circe-Light'), url('../fonts/Circe-Light.eot?#iefix') format('embedded-opentype'), url('../fonts/Circe-Light.woff') format('woff'), url('../fonts/Circe-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Circe Bold';
  src: url('../fonts/Circe-Bold.eot');
  src: local('Circe Bold'), local('Circe-Bold'), url('../fonts/Circe-Bold.eot?#iefix') format('embedded-opentype'), url('../fonts/Circe-Bold.woff') format('woff'), url('../fonts/Circe-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Circe ExtraBold';
  src: url('../fonts/Circe-ExtraBold.eot');
  src: local('Circe ExtraBold'), local('Circe-ExtraBold'), url('../fonts/Circe-ExtraBold.eot?#iefix') format('embedded-opentype'), url('../fonts/Circe-ExtraBold.woff') format('woff'), url('../fonts/Circe-ExtraBold.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
#body {
  font-family: 'Circe Regular';
}
h1 {
  max-width: 900px;
  font-family: 'Circe ExtraBold';
  font-size: 40px;
  font-weight: 400;
  line-height: 26px;
  color: var(--color-title);
}
h2 {
  font-family: 'Circe Regular';
  font-size: 30px;
  font-weight: 700;
  line-height: 26px;
  margin-bottom: 17px;
}
h3 {
  color: var(--color-title);
  font-family: 'Circe Regular';
  font-size: 25px;
  font-weight: 700;
  margin: 15px 0 10px;
}
.sub-wrapper {
  background: #ccc;
  position: absolute;
  left: -100%;
}
.wrapper {
  background: yellow;
}
#block-sitebranding img {
  width: 75px;
  height: 75px;
}
.container {
  border: 1px dotted;
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
}
.sub-container {
  border: 1px dotted;
  max-width: 250px;
}
:root {
  --white: #fff;
  --views-buton-hover: #2a4dd5;
  --color-title: #25292c;
  --color-secondary-text: #838798;
  --theme-bright-bg: #f9b607;
  --theme-dark-bg: #25292c;
  --color: #454545;
  --button-hover: rgba(42, 77, 213, 0.4);
  --shadow-bright: rgba(249, 182, 7, 0.4);
  --textarea-bg: #9fa5b7;
  --textarea-input-border: #eee;
  --line-height: 1.5;
  --font-size-base: 1rem;
  /* 1rem = 16px if font root is 100% ands browser defaults are used. */
  --font-size-h1: 2.027rem;
  /* ~32px */
  --font-size-h2: 1.802rem;
  /* ~29px */
  --font-size-h3: 1.602rem;
  /* ~26px */
  --font-size-h4: 1.424rem;
  /* ~23px */
  --font-size-h5: 1.266rem;
  /* ~20px */
  --font-size-h6: 1.125rem;
  /* 18px */
  --font-size-sm: 0.889rem;
  /* ~14px */
  --font-size-xs: 0.79rem;
  /* ~13px */
  --font-size-xxs: 0.702rem;
  /* ~11px */
  --font-family: BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  --font-monospace: "JetBrains Mono", "Fira Code", SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}
.greed-row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  justify-content: center;
}
.greed-items {
  width: calc(100% / 12 * 6 - 30px);
  margin: 15px;
}
/*'light' or 'dark' options */
/**
 * Form
 */
form {
  padding-bottom: 60px;
}
form label {
  color: var(--textarea-bg);
  font-family: 'Circe Regular';
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
  display: block;
  position: relative;
  padding-left: 20px;
}
#edit-message-wrapper label {
  padding: 0;
}
#edit-name label:before,
.js-form-item-name label:before {
  content: '';
  width: 15px;
  height: 15px;
  background: url(../images/icons/name-yellow.svg) no-repeat;
  position: absolute;
  left: 0px;
  top: 5px;
}
#edit-mail label:before,
.js-form-item-mail label:before {
  content: '';
  width: 15px;
  height: 15px;
  background: url(../images/icons/emai-yellow.svg) no-repeat;
  position: absolute;
  left: 0px;
  top: 5px;
  background-size: contain;
}
#edit-subject-wrapper label:before {
  content: '';
  width: 15px;
  height: 15px;
  background: url(../images/icons/organization-yellow.svg) no-repeat;
  position: absolute;
  left: 0px;
  top: 5px;
  background-size: contain;
}
textarea,
input {
  border: 1px solid var(--textarea-input-border);
  border-radius: 6px;
}
input {
  outline: 0;
  padding: 5px;
}
textarea {
  box-sizing: border-box;
  max-width: 900px;
  width: 100%;
  color: var(--textarea-bg);
  font-family: 'Circe Regular';
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
  box-shadow: 0 21px 35px rgba(49, 53, 58, 0.04);
  border-radius: 6px;
  background-color: var(--white);
  outline: none;
  padding: 16px 20px 32px;
}
#edit-mail,
#edit-name,
#edit-subject-wrapper {
  max-width: 48%;
  width: 100%;
  vertical-align: top;
}
#edit-subject-wrapper input {
  width: 100%;
}
#edit-submit,
.edit-submit a {
  padding: 19px 72px;
  position: relative;
  border-radius: 6px;
  box-shadow: 0 21px 35px rgba(249, 182, 7, 0.19);
  background-color: var(--theme-bright-bg);
  color: var(--color-title);
  font-family: 'Circe Regular';
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  border-bottom: none;
  transition: box-shadow 0.5s;
}
.edit-submit {
  border-radius: 6px;
  border: 1px solid var(--textarea-input-border);
  opacity: 0.7;
  display: inline-block;
  padding: 15px;
  height: 100px;
  margin-bottom: 60px;
}
#edit-submit:hover {
  box-shadow: 0 31px 45px rgba(249, 182, 7, 0.39);
}
#edit-actions {
  padding: 20px 0;
}
#block-gruzservice-sitebranding {
  display: flex;
  width: 290px;
  margin-left: auto;
  padding: 10px 0;
}
#block-gruzservice-sitebranding img {
  width: 55px;
  height: 55px;
}
.site-branding__text {
  margin-left: 10px;
  margin-top: 5px;
}
.site-branding__slogan {
  color: var(--color-secondary-text);
}
.site-branding__name a {
  display: inline-block;
  color: var(--color-secondary-text);
  text-decoration: none;
  font-family: 'Circe ExtraBold';
  font-size: 21px;
  font-weight: 400;
  line-height: 26px;
}
/**
 * Mobile menu
 */
#block-mainmenu ul {
  margin: 0;
  padding: 0;
}
#block-mainmenu ul li {
  list-style: none;
}
#block-mainmenu ul li a {
  font-size: 1.1rem;
  text-transform: uppercase;
  width: 100%;
  display: inline-block;
  font-weight: bold;
  text-decoration: none;
}
#block-mainmenu {
  position: fixed;
  left: -100%;
  -webkit-transition: left 0.7s;
  transition: left 0.7s;
  padding: 25%;
  z-index: 2;
  width: 100%;
  height: 100vh;
  top: 0;
  background: var(--theme-bg);
}
.info-child-header {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
#block-brendingsayta img {
  width: 75px;
  height: 75px;
}
#block-brendingsayta {
  font-size: 0.5rem;
  color: var(--color);
  font-family: sans-serif;
}
#block-brendingsayta a {
  display: block;
  font-size: 1.7rem;
  font-weight: bold;
  color: var(--color);
  font-family: fantasy;
  text-decoration: none;
}
.info-child-header {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
input#isexpanded {
  display: none;
}
label#expand-btn,
label#expand-btn:after,
label#expand-btn:before {
  width: 30px;
  height: 5px;
  background-color: var(--color);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 2px;
}
label#expand-btn {
  position: absolute;
  top: 30px;
  right: 15px;
  z-index: 3;
}
label#expand-btn:after,
label#expand-btn:before {
  content: "";
  position: absolute;
  left: 0;
  top: -9px;
}
label#expand-btn:after {
  top: 9px;
}
#isexpanded:checked ~ #expand-btn {
  background-color: transparent;
}
#isexpanded:checked ~ #expand-btn:after {
  -webkit-transform: rotate(-45deg) translate(7px, -8px);
  transform: rotate(-45deg) translate(7px, -8px);
}
#isexpanded:checked ~ #expand-btn:before {
  -webkit-transform: rotate(45deg) translate(5px, 5px);
  transform: rotate(45deg) translate(5px, 5px);
}
#isexpanded:checked ~ * #block-mainmenu {
  left: -1px;
}
/**
 * Block
 *
