   */
/**
   * Breadcrumb
   */
#breadcrumbs {
  margin-top: 20px;
}
#breadcrumbs li {
  margin-left: 5px;
  display: inline-block;
  color: var(--color-secondary-text);
  font-family: 'Circe Regular';
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
}
#breadcrumbs a {
  text-decoration: none;
  color: var(--views-buton-hover);
  font-weight: 700;
  font-family: 'Circe Bold';
}
#breadcrumbs a:after {
  content: '»';
  padding-left: 3px;
}
/**
 * Work
 */
.work {
  margin: 67px 0 80px;
}
.work header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}
.work header a {
  display: inline-block;
  text-decoration: none;
  color: var(--color-secondary-text);
  font-family: 'Circe Regular';
  font-size: 16px;
  font-weight: 700;
  line-height: 26px;
}
.work header a::after {
  display: inline-block;
  content: '';
  width: 14px;
  height: 10px;
  margin-left: 22px;
  background-image: url(../images/icons/arrow-pointing-to-right.svg);
}
.work ul {
  display: flex;
  flex-wrap: wrap;
  box-shadow: 0 21px 35px rgba(49, 53, 58, 0.04);
  background-color: var(--white);
  list-style: none;
}
.work ul li {
  position: relative;
  padding: 35px 50px 10px 30px;
}
.work h3 {
  color: var(--color-title);
  font-family: 'Circe Regular';
  font-size: 25px;
  font-weight: 700;
  margin: 15px 0 10px;
}
.work p {
  color: var(--color-secondary-text);
  font-family: 'Circe Regular';
  font-size: 16px;
  font-weight: 400;
  line-height: initial;
}
.work ul li:first-child::after {
  position: absolute;
  bottom: 0;
  right: 0;
  display: block;
  content: '';
  width: 1px;
  height: 146px;
  background-color: var(--theme-bright-bg);
  opacity: 0.25;
}
.work ul li:nth-child(2)::after {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  content: '';
  width: 1px;
  height: 146px;
  background-color: var(--theme-bright-bg);
  opacity: 0.25;
}
.work li img {
  width: 70px;
  height: 70px;
  margin: auto;
  display: block;
}
/**
   * Prices
   */
.prices header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.prices header h2 {
  margin-bottom: 0;
}
.prices .button-wrap button {
  padding: 18px 40px;
  background: none;
  border: 2px solid var(--theme-bright-bg);
}
.button-wrap button {
  transition: all 1s;
  border-radius: 6px;
  outline: none;
}
.button-wrap button b {
  color: var(--color-title);
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  border-bottom: 1px dashed rgba(37, 41, 44, 0.3);
}
.prices table {
  border-collapse: separate;
  margin: 50px 0;
  box-shadow: 0 21px 35px rgba(49, 53, 58, 0.04);
  border-radius: 4px;
  background-color: var(--white);
  overflow-x: scroll;
  display: block;
}
.prices table thead {
  background-color: var(--theme-dark-bg);
}
.prices table thead th:first-child {
  border-top-left-radius: 6px;
}
.prices table thead th:last-child {
  border-top-right-radius: 6px;
}
.prices table tr:nth-child(even) {
  background-color: #f9f9fa;
}
.prices table thead th {
  text-align: left;
  padding: 18px 27px;
  color: var(--white);
  font-family: 'Circe Regular';
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
}
.prices table tbody td {
  padding: 29px 30px;
  color: #474e6f;
  font-family: 'Circe Regular';
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}
/**
 * Description-service
 */
.description-service {
  padding-bottom: 40px;
  margin-top: 95px;
}
.description-service h3 {
  margin: 40px 0;
  color: #474e6f;
  font-family: 'Circe Regular';
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}
.text-and-list-wrap {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
  flex-wrap: wrap;
}
.list-of-services {
  flex-shrink: 0;
  width: 324px;
  padding: 10px 25px;
  border-radius: 6px;
  border: 15px solid var(--white);
  background-color: #fafaf8;
  list-style: none;
  margin: 0 auto;
}
.list-of-services li {
  position: relative;
  color: var(--color-secondary-text);
  font-family: 'Circe Regular';
  font-size: 18px;
  font-weight: 400;
  line-height: 48px;
}
.list-of-services li::before {
  display: inline-block;
  margin-right: 15px;
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 2px solid var(--theme-bright-bg);
}
/**
 * Contacts
 */
.map-wrap {
  margin-bottom: 96px;
  display: flex;
  box-shadow: 0 21px 35px rgba(49, 53, 58, 0.04);
  border-radius: 6px;
  background-color: var(--white);
}
.map {
  flex: 1;
  flex-basis: 300px;
}
.map-wrap .contacts-wrap {
  padding: 58px 15px 25px 24px;
  flex: 1;
}
.map-wrap .contacts-wrap h2 {
  color: #474e6f;
  font-family: 'Circe Bold';
  font-size: 18px;
  font-weight: 700;
  font-weight: 400;
  padding: 0;
}
.map-wrap .contacts-wrap p {
  margin: 26px 0;
  color: var(--color-secondary-text);
  font-family: 'Circe Regular';
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}
.map-wrap .contacts-wrap b {
  color: #474e6f;
  font-family: 'Circe Bold';
  font-weight: 700;
}
.adress,
.tel {
  position: relative;
  padding-left: 15px;
}
.map-wrap .adress:before {
  content: '';
  width: 15px;
  height: 15px;
  background: url(../images/icons/address-contacts.svg) no-repeat;
  position: absolute;
  left: -6px;
  top: 2px;
  background-size: contain;
}
.map-wrap .tel:before {
  content: '';
  width: 15px;
  height: 15px;
  background: url(../images/icons/phone-contacts.svg) no-repeat;
  position: absolute;
  left: -6px;
  top: 2px;
  background-size: contain;
}
.all-services .views-row {
  position: relative;
  color: var(--white);
  background-color: var(--color-title);
  border-radius: 6px;
  box-shadow: 0 21px 35px var(--shadow-bright);
  margin-bottom: 20px;
  max-width: 517px;
}
.all-services .views-row:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background-image: linear-gradient(-30deg, var(--theme-bright-bg) 0%, rgba(255, 255, 255, 0) 100%);
  opacity: 0.25;
  transition: opacity 1s;
}
.all-services .views-row:hover::after {
  opacity: 0;
}
.all-services h2 {
  position: absolute;
  top: 10px;
  color: var(--white);
  padding: 0 15px;
}
.all-services a {
  color: var(--white);
  text-decoration: none;
  z-index: 1;
  position: relative;
}
.all-services img {
  opacity: 0.3;
  width: 100%;
}
.all-services .description {
  position: absolute;
  top: 70px;
  padding: 0 15px;
}
.views-button {
  padding: 0 15px;
  position: absolute;
  bottom: 30px;
  left: 0;
}
.min-time {
  padding: 0 15px;
  right: 90px;
  bottom: 30px;
  position: absolute;
}
.min-time:before {
  content: '';
  background: url(../images/icons/time.svg) no-repeat;
  width: 15px;
  height: 15px;
  left: -4px;
  top: 2px;
  position: absolute;
}
.views-price {
  position: absolute;
  bottom: 30px;
  right: 15px;
}
.views-price:before {
  content: '';
  background: url(../images/icons/price.svg) no-repeat;
  width: 20px;
  height: 20px;
  left: -24px;
  top: -2px;
  position: absolute;
  background-size: cover;
}
.views-button a {
  padding: 12px;
  border-radius: 6px;
  border: 2px solid var(--theme-bright-bg);
  transition: box-shadow 1s, background-color 1s, border-color 1s;
  text-decoration: none;
  box-shadow: 0 21px 35px var(--shadow-bright);
  font-size: 16px;
  font-weight: 700;
  line-height: 26px;
}
.views-button a:after {
  display: inline-block;
  content: '';
  width: 14px;
  height: 10px;
  margin-left: 22px;
  background-image: url('../images/icons/arrow-pointing-to-right.svg');
}
.container {
  padding: 0 20px;
}
/**
 * Node
 */
.node-images {
  position: absolute;
  right: 0;
  top: -30%;
  overflow: hidden;
  width: 820px;
  height: 820px;
}
.node-images img {
  position: relative;
  right: -38%;
  top: 80px;
  width: 80%;
  height: auto;
}
.tizer {
  color: var(--color);
  line-height: 22px;
}
.tizer,
.node-price,
.node-time {
  max-width: 500px;
  width: 100%;
}
.node-price {
  color: #1a1a1a;
  font-family: 'Circe ExtraBold';
  font-size: 26px;
  font-weight: 400;
  line-height: 26px;
}
.node-time,
.node-price {
  text-align: right;
}
.container p {
  padding: 15px 5px;
  font-size: 18px;
  font-family: 'Circe Regular';
  color: var(--color);
  line-height: 28px;
}
/**
 * Aside
 */
aside,
aside a {
  color: var(--white);
}
.contacts {
  width: 280px;
  margin-top: 40px;
}
.contacts img {
  z-index: 10;
  width: 15px;
  height: 15px;
  margin-right: 30px;
}
.contacts a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 700;
  line-height: 16px;
  border-bottom: 1px solid transparent;
  transition: all 1s;
}
.contacts p {
  display: inline-block;
  line-height: 26px;
  margin-top: -33px;
}
.contacts p:before {
  top: 11px;
}
.contacts a::after,
.contacts p::after {
  left: -20px;
  top: -20px;
  width: 55px;
  height: 55px;
  opacity: 0.15;
}
aside a:hover {
  color: var(--theme-bright-bg);
  border-color: var(--theme-bright-bg);
}
.contacts a::before,
.contacts a::after,
.contacts p::before,
.contacts p::after {
  position: absolute;
  display: block;
  content: '';
  background-color: var(--views-buton-hover);
  border-radius: 50%;
}
.contacts a::before,
.contacts p::before {
  left: -7px;
  top: -6px;
  width: 30px;
  height: 30px;
}
.contacts .phone a {
  font-family: 'Circe ExtraBold';
  font-size: 21px;
  font-weight: 400;
}
.contacts li {
  position: relative;
  margin: 52px 0 58px;
  display: flex;
  align-items: center;
}
aside .button-wrap::after {
  bottom: 0;
  left: -150px;
}
aside .button-wrap::before,
aside .button-wrap::after {
  content: '';
  position: absolute;
  display: block;
  width: 360px;
  height: 1px;
  background-color: rgba(37, 41, 44, 0.3);
}
aside .button-wrap::before {
  top: 0;
  right: 0;
}
aside .button-wrap {
  position: relative;
  margin-top: 60px;
}
aside .button-wrap button {
  margin-right: 15px;
  box-shadow: 0 21px 35px rgba(249, 182, 7, 0.19);
  background-color: var(--theme-bright-bg);
}
.button-wrap button {
  padding: 18px 20px;
  transition: all 1s;
  border-radius: 6px;
  border: none;
  outline: none;
}
aside .button-wrap button:hover {
  box-shadow: 0 31px 45px rgba(249, 182, 7, 0.39);
}
aside .button-wrap button img {
  margin-right: 16px;
  width: 15px;
  height: 15px;
}
.button-wrap button b {
  color: var(--color-title);
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  border-bottom: 1px dashed rgba(37, 41, 44, 0.3);
}
aside nav {
  margin-top: 60px;
}
aside ul {
  list-style: none;
}
aside nav li {
  position: relative;
  padding-left: 50px;
}
aside nav li::before {
  content: '';
  position: absolute;
  top: 30px;
  left: 0;
  display: block;
  width: 12px;
  height: 1px;
  background-color: var(--theme-bright-bg);
}
aside nav a {
  font-size: 22px;
  font-weight: 400;
  line-height: 60px;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  border-bottom-color: transparent;
  transition: all 1s;
  padding: 15px 0;
}
aside nav a:hover,
.is-active {
  color: var(--theme-bright-bg);
  border-color: var(--theme-bright-bg);
}
.wrapper,
.sub-wrapper {
  background: unset;
}
.container,
.sub-container {
  border: none;
}
.wrapper {
  background-color: #fafaf8;
}
.sub-wrapper {
  background-color: var(--theme-dark-bg);
  min-width: 300px;
  padding: 0 10px;
}
.panel {
  position: sticky;
}
.fixed_block {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
}
.sub-container {
  max-width: 266px;
}
/**
 * Footer
 */
footer .wrapper {
  margin: 30px 0px;
  background: var(--white);
}
footer .wrapper p {
  opacity: 0.5;
  color: var(--color-secondary-text);
  font-family: 'Circe Regular';
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  padding: 0;
}
@media screen and (min-width: 1025px) {
  .sub-wrapper {
    flex: 1;
  }
  #expand-btn {
    display: none;
  }
}
@media screen and (min-width: 769px) {
  /**
    * Layout
    **/
  footer,
  main,
  header {
    display: flex;
    flex-wrap: wrap;
  }
  .sub-wrapper {
    position: unset;
  }
  .wrapper {
    flex: 3;
  }
  .fixed_block {
    position: unset;
  }
  .work ul {
    flex-wrap: nowrap;
  }
  .prices table {
    overflow-x: inset;
    display: table;
  }
  .sub-container {
    margin-left: auto;
  }
  .list-of-services {
    margin-left: 35px;
    padding: 10px 35px;
  }
  .all-services {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    justify-content: center;
  }
  .all-services .views-row {
    width: calc(100% / 12 * 6 - 30px);
    margin: 15px;
  }
  .all-services img {
    opacity: 0.3;
    width: 100%;
  }
  .views-button a {
    padding: 12px 36px;
  }
  .views-button a:hover {
    box-shadow: 0 21px 35px var(--button-hover);
    background-color: var(--views-buton-hover);
    border-color: var(--views-buton-hover);
  }
}
